#maincontent {
    background: var(--branco-primario) 0% 0% no-repeat padding-box;
    box-shadow: -2px 3px 5px #00000033;
    border: 1px solid #36394033;
    border-radius: 0px 4px 4px 0px;

    @import "3-main/51-maincontent-dashboard_cards";
}


@media only screen and (max-width: 432px)  {
    #maincontent {
        background: none;
        box-shadow: none;
        border: none;

        @import "3-main/51-maincontent-dashboard_cards";
    }
}